export default {
	data() {
		this.page = 1
		return {
			logList: [],
		};
	},
	onLoad() {
		this.getLog()
	},
	onReachBottom() {
		this.page++
		this.getLog()
	},
	methods: {
		getLog() {
			this.$app.request(this.$app.API.LOG, {
				page: this.page
			}, res => {
				if (this.page == 1) {
					this.logList = res.data
				} else {
					this.logList = this.logList.concat(res.data)
				}
			})
		},
	}
}
